import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Jobs from '../views/jobs/Jobs.vue'
import UiUxJobs from '../views/jobs/UiUxJobs.vue'
import FrontendDeveloper from '../views/jobs/FrontendDeveloper.vue'
import BackendDeveloper from '../views/jobs/BackendDeveloper.vue'
import Page404 from '../views/Page404.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/jobs',
    name: 'Jobs',
    component: Jobs
  },
  {
    path: '/jobs/UiUxJobs',
    name: 'UiUxJobs',
    component: UiUxJobs
  },
  {
    path: '/jobs/FrontendDeveloper',
    name: 'FrontendDeveloper',
    component: FrontendDeveloper
  },
  {
    path: '/jobs/BackendDeveloper',
    name: 'BackendDeveloper',
    component: BackendDeveloper
  },
  //catchAll 404 Page
  {
    path: '/:catchAll(.*)',
    name: 'Page404',
    component: Page404
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
